<template>
    <div class="dynamic-main">
        <el-card class="main-header" shadow="never">好友动态</el-card>
        <div class="posts" v-if="initData.length">
            <el-card class="post-card" v-for="(post, index) in initData" :key="index" shadow="hover">
                <el-row :gutter="20">
                    <el-col :span="6" class="author-info">
                        <router-link :to="`/Athor/${post.author._id}`" style="text-decoration: none;">
                            <img :src="post.author.image" alt="Author Image" class="author-image" loading="lazy"/>
                            <span class="name">{{ post.author.name }}</span>
                        </router-link>
                    </el-col>
                    <el-col :span="18">
                        <div class="content">
                            <router-link :to="`/Post/${post._id}`" style="text-decoration: none;">
                                <h3>{{ post.title }}</h3>
                            </router-link>
                            <p>{{ post.summary }}……</p>
                            <div class="metadata">
                                <span class="create_time">{{ change_date(post.create_time) }}</span>
                                <span class="views_count"> | 浏览 {{ post.views_count }}</span>
                            </div>
                        </div>
                    </el-col>
                </el-row>

                <div class="interactions">
                    <el-button link icon="el-icon-chat-dot-round">评论</el-button>
                    <el-button link icon="el-icon-star-off">收藏</el-button>
                    <el-button link icon="el-icon-share">分享</el-button>
                </div>
            </el-card>
        </div>
        <el-empty description="这里什么都没有" v-else></el-empty>
    </div>
</template>
<script>
import { ref, onMounted } from 'vue';
import { useUrl } from '@/store/useUrl';
import { get } from '@/http/httpRequest';

export default {
    setup() {
        const urls = useUrl();
        const initData = ref([]);

        const initPage = async () => {
            const url = urls.FriendsDynamic;
            try {
                const response = await get(url);
                if (response) {
                    initData.value = sort_posts(response.data);
                }
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        const sort_posts = (array) => {
            return [...array].sort((a, b) => new Date(b.create_time) - new Date(a.create_time));
        };

        const change_date = (str) => {
            const date = new Date(str);
            return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')} ${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2, '0')}:${String(date.getSeconds()).padStart(2, '0')}`;
        };

        onMounted(async function () {
            await initPage();
        });

        return {
            initData,
            change_date
        };
    }
}
</script>
